import React, { useEffect } from 'react'
//CSS
import './PolicyPrivacy__6.css'
//Packages
import Aos from 'aos'
import "aos/dist/aos.css" 

function PolicyPrivacy__6() {
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="policyprivacy__6__container">
        <h1 data-aos='fade-down' data-aos-duration='1500' className='h1__policyprivacy__2'>Informacje w formularzach</h1>
        <ol className='ol__policyprivacy'>
            <li data-aos='fade-right' data-aos-duration='1500' className='li__policyprivacy'>Serwis zbiera informacje podane dobrowolnie przez uytkownika, w tym dane osobowe, o ile zostan one podane.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className='li__policyprivacy'>Serwis moe zapisa informacje o parametrach poczenia (oznaczenie czasu, adres IP).</li>
            <li data-aos='fade-right' data-aos-duration='1500' className='li__policyprivacy'>Serwis, w niektrych wypadkach, moe zapisa informacj uatwiajc powizanie danych w formularzu z adresem e-mail uytkownika wypeniajcego formularz. W takim wypadku adres e-mail uytkownika pojawia si wewntrz adresu url strony zawierajcej formularz.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className='li__policyprivacy'>Dane podane w formularzu s przetwarzane w celu wynikajcym z funkcji konkretnego formularza, np. w celu dokonania procesu obsugi zgoszenia serwisowego lub kontaktu handlowego, rejestracji usug itp. Kadorazowo kontekst i opis formularza w czytelny sposb informuje, do czego on suy.</li>
        </ol>
    </div>
  )
}

export default PolicyPrivacy__6