import React, { useEffect } from 'react'
//CSS
import './PriceWebsite.css'
//Images 
import PriceImage from '../../../Images/17.png'
//Packages
import $ from 'jquery'
import VanillaTilt from 'vanilla-tilt'
import Aos from 'aos'
import "aos/dist/aos.css" 

function PriceWebsite() {
  //VanillaTilt
  $(document).ready(function Main() { VanillaTilt.init(document.querySelectorAll(".price__img__1")); });
  $(function() { VanillaTilt.init(document.querySelectorAll(".price__img__1")); });
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="pricewebsite__container">
    <div className="price__row">
        <div className="price__column__1">
            <h1 data-aos='fade-right' data-aos-duration='1500' className='h1__price__1'>Cena za nasze usugi</h1>
            <h2 data-aos='fade-right' data-aos-duration='1500' className='h2__price__1'>Cennik naszych usug nie jest ciele okrelony, natomiast jeli by by, byo by to skrajnie nieprofesjonalne, poniewa nie moemy pozwoli sobie na stworzenie strony w cenie sklepu internetowego. Cena strony zaley od wielu czynnikw min. od typu strony (blog, strona wizytwkowa firmy, portfolio, sklep), iloci treci zawartych na stronie, jakim sposobem jest tworzona strona (WordPress - System CMS polegajcy na przeciganiu elementw / React - System JSX, strona jest pisana kodem przez zesp programistw). Jeli jeste zainteresowany/a naszymi usugami prosz o skontaktowanie si z nami poprzez Chat albo formularz kontaktowy.</h2>
        </div>
        <div data-aos='fade-left' data-aos-duration='1500' className="price__column__2">
            <img className='price__img__1' src={PriceImage} alt=''></img>
        </div>
    </div>
</div>
  )
}

export default PriceWebsite