import React, { useEffect } from 'react'
//CSS
import './PolicyPrivacy__3.css'
//Packages
import Aos from 'aos'
import "aos/dist/aos.css" 

function PolicyPrivacy__3() {
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="policyprivacy__3__container">
        <h1 data-aos='fade-down' data-aos-duration='1500' className="h1__policyprivacy__2">Wybrane metody ochrony danych stosowane przez operatora</h1>
        <ol className='ol__policyprivacy'>
            <li data-aos='fade-left' data-aos-duration='1500' className='li__policyprivacy'>Miejsca logowania i wprowadzania danych osobowych s chronione w warstwie transmisji (certyfikat SSL). Dziki temu dane osobowe i dane logowania, wprowadzone na stronie, zostaj zaszyfrowane w komputerze uytkownika i mog by odczytane jedynie na docelowym serwerze.</li>
            <li data-aos='fade-right' data-aos-duration='1500' className='li__policyprivacy'>Dane osobowe przechowywane w bazie danych s zaszyfrowane w taki sposb, e jedynie posiadajcy Operator klucz moe je odczyta. Dziki temu dane s chronione na wypadek wykradzenia bazy danych z serwera.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className='li__policyprivacy'>Hasa uytkownikw s przechowywane w postaci hashowanej. Funkcja hashujca dziaa jednokierunkowo - nie jest moliwe odwrcenie jej dziaania, co stanowi obecnie wspczesny standard w zakresie przechowywania hase uytkownikw.</li>
            <li data-aos='fade-right' data-aos-duration='1500' className='li__policyprivacy'>Operator okresowo zmienia swoje hasa administracyjne.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className='li__policyprivacy'>W celu ochrony danych Operator regularnie wykonuje kopie bezpieczestwa.</li>
            <li data-aos='fade-right' data-aos-duration='1500' className='li__policyprivacy'>Istotnym elementem ochrony danych jest regularna aktualizacja wszelkiego oprogramowania, wykorzystywanego przez Operatora do przetwarzania danych osobowych, co w szczeglnoci oznacza regularne aktualizacje komponentw programistycznych.</li>
        </ol>
    </div>
  )
}

export default PolicyPrivacy__3