import React, { useEffect } from 'react'
//CSS
import './PolicyPrivacy__9.css'
//Packages
import Aos from 'aos'
import "aos/dist/aos.css" 

function PolicyPrivacy__9() {
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="policyprivacy__9__container">
        <h1 data-aos='fade-down' data-aos-duration='1500' className="h1__policyprivacy__2">Informacja o plikach cookies</h1>
        <ol className="ol__policyprivacy">
            <li data-aos='fade-right' data-aos-duration='1500' className="li__policyprivacy">Serwis korzysta z plikw cookies.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className="li__policyprivacy">Pliki cookies (tzw. ciasteczka) stanowi dane informatyczne, w szczeglnoci pliki tekstowe, ktre przechowywane s w urzdzeniu kocowym Uytkownika Serwisu i przeznaczone s do korzystania ze stron internetowych Serwisu. Cookies zazwyczaj zawieraj nazw strony internetowej, z ktrej pochodz, czas przechowywania ich na urzdzeniu kocowym oraz unikalny numer.</li>
            <li data-aos='fade-right' data-aos-duration='1500' className="li__policyprivacy">Podmiotem zamieszczajcym na urzdzeniu kocowym Uytkownika Serwisu pliki cookies oraz uzyskujcym do nich dostp jest operator Serwisu.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className="li__policyprivacy">Pliki cookies wykorzystywane s w nastpujcych celach:</li>
            <ol className="ol__policyprivacy__2">
                <li data-aos='fade-right' data-aos-duration='1500' className="li__policyprivacy">utrzymanie sesji uytkownika Serwisu (po zalogowaniu), dziki ktrej uytkownik nie musi na kadej podstronie Serwisu ponownie wpisywa loginu i hasa;</li>
                <li data-aos='fade-left' data-aos-duration='1500' className="li__policyprivacy">realizacji celw okrelonych powyej w czci "Istotne techniki marketingowe";</li>
            </ol>
            <li data-aos='fade-right' data-aos-duration='1500' className="li__policyprivacy">W ramach Serwisu stosowane s dwa zasadnicze rodzaje plikw cookies: sesyjne (session cookies) oraz stae (persistent cookies). Cookies sesyjne s plikami tymczasowymi, ktre przechowywane s w urzdzeniu kocowym Uytkownika do czasu wylogowania, opuszczenia strony internetowej lub wyczenia oprogramowania (przegldarki internetowej). Stae pliki cookies przechowywane s w urzdzeniu kocowym Uytkownika przez czas okrelony w parametrach plikw cookies lub do czasu ich usunicia przez Uytkownika.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className="li__policyprivacy">Oprogramowanie do przegldania stron internetowych (przegldarka internetowa) zazwyczaj domylnie dopuszcza przechowywanie plikw cookies w urzdzeniu kocowym Uytkownika. Uytkownicy Serwisu mog dokona zmiany ustawie w tym zakresie. Przegldarka internetowa umoliwia usunicie plikw cookies. Moliwe jest take automatyczne blokowanie plikw cookies Szczegowe informacje na ten temat zawiera pomoc lub dokumentacja przegldarki internetowej.</li>
            <li data-aos='fade-right' data-aos-duration='1500' className="li__policyprivacy">Ograniczenia stosowania plikw cookies mog wpyn na niektre funkcjonalnoci dostpne na stronach internetowych Serwisu.</li>
            <li data-aos='fade-left' data-aos-duration='1500' className="li__policyprivacy">Pliki cookies zamieszczane w urzdzeniu kocowym Uytkownika Serwisu wykorzystywane mog by rwnie przez wsppracujce z operatorem Serwisu podmioty, w szczeglnoci dotyczy to firm: Google (Google Inc. z siedzib w USA), Facebook (Facebook Inc. z siedzib w USA), Twitter (Twitter Inc. z siedzib w USA).</li>
        </ol>
    </div>
  )
}

export default PolicyPrivacy__9