import React, { useEffect } from 'react'
//CSS
import './BlogSupport.css'
//Images
import BlogSupportImage from '../../../Images/22.png'
//Packages
import $ from 'jquery'
import VanillaTilt from 'vanilla-tilt'
import Aos from 'aos'
import "aos/dist/aos.css" 

function BlogSupport() {
  //VanillaTilt
  $(document).ready(function Main() { VanillaTilt.init(document.querySelectorAll(".blogsupport__img__1")); });
  $(function() { VanillaTilt.init(document.querySelectorAll(".blogsupport__img__1")); });
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="blogsupport__container">
        <h1 data-aos='fade-down' data-aos-duration='1500' className='h1__blogsupport__1'>Wsparcie nad blogiem</h1>
        <div className="blogsupport__row">
            <div data-aos='fade-right' data-aos-duration='1500' className="blogsupport__column__1">
                <img className='blogsupport__img__1' src={BlogSupportImage} alt=''></img>
            </div>
            <div data-aos='flip-up' data-aos-duration='1500' className="blogsupport__column__2">
                <h2 className='h2__blogsupport__1'>W swoich usugach oferujemy profesjonalne wsparcie i opiek nad stron ktra jest wliczona w koszty przez pierwszy.</h2>
                <h2 className='h2__blogsupport__1'>Oferujemy krtkie przeszkolenie w zakresie zarzdzania blogiem.</h2>
                <h2 className='h2__blogsupport__1'>Jeli kto nie ma czasu, ani chci na samodzielne wstawianie lub edytowanie treci i zarzdzanie swoj stron, mamy w swojej ofercie za nisk kwot zarzdzanie ktre jest patne z gry za miesic.</h2>
                <h2 className='h2__blogsupport__1'>W zarzdzenie stron wchodzi edycja lub wstawianie nowych tekstw, zdj, edytowanie kodu rdowego, staa optymalizacja strony, oraz dopasowywanie responsywnoci pod wszystkie urzdzenia mobilne.</h2>
            </div>
            <div data-aos='fade-left' data-aos-duration='1500' className="blogsupport__column__3">
                <img className='blogsupport__img__1' src={BlogSupportImage} alt=''></img>
            </div>
        </div>
    </div>
  )
}

export default BlogSupport