import React, { useEffect } from 'react'
//CSS
import './GraphicSkills.css'
//Images
import GraphicSkillsImage from '../../../Images/37.png'
//Packages
import $ from 'jquery'
import VanillaTilt from 'vanilla-tilt'
import Aos from 'aos'
import "aos/dist/aos.css" 

function GraphicSkills() {
  //VanillaTilt
  $(document).ready(function Main() { VanillaTilt.init(document.querySelectorAll(".graphicskills__img__1")); });
  $(function() { VanillaTilt.init(document.querySelectorAll(".graphicskills__img__1")); });
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="graphicskills__container">
        <div className="graphicskills__row">
            <div data-aos='fade-right' data-aos-duration='1500' className="graphicskills__column__1">
                <img className='graphicskills__img__1' src={GraphicSkillsImage} alt=''></img>
            </div>
            <div className="graphicskills__column__2">
                <h1 data-aos='fade-left' data-aos-duration='1500' className="h1__graphicskills__1">Co powinien potrafi grafik?</h1>
                <h2 data-aos='fade-left' data-aos-duration='1500' className="h2__graphicskills__1">Przede wszystkim musi by kreatywny, mie zmys artystyczny i wysoko rozwinite wyczucie estetyki.</h2>
                <h2 data-aos='fade-left' data-aos-duration='1500' className="h2__graphicskills__1">Grafik komputerowy powinien ponadto sprawnie posugiwa si komputerem, w tym umie korzysta z programw takich jak Adobe Photoshop, Adobe inDesign, Corel Draw czy Adobe Illustrator, ktre s nieodcznym elementem jego pracy. Tak naprawd grafikw komputerowych mona podzieli na dwie grupy: warsztatowcw oraz wirtuozw.</h2>
                <h2 data-aos='fade-left' data-aos-duration='1500' className="h2__graphicskills__1">Grafik komputerowy powinien poza tym umie pracowa pod presj czasu, by komunikatywnym, a take by na bieco z nowymi trendami ze wiata multimediw, marketingu, designu, reklamy czy oglnie pojtego projektowania graficznego. Dodatkowym atutem jest zawsze znajomo CSS, HTML oraz innych jzykw programowania.</h2>
            </div>
        </div>
    </div>
  )
}

export default GraphicSkills