import React, { useEffect } from 'react'
//CSS
import './ServicesPositioning.css'
//Packages
import Aos from 'aos'
import "aos/dist/aos.css" 

function ServicesPositioning() {
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="servicespositioning__container">
        <h1 data-aos='fade-down' data-aos-duration='1500' className='h1__servicespositioning__1'>Poznaj nasze pozycjonowanie</h1>
        <div className="servicespositioning__row">
            <div data-aos='fade-right' data-aos-duration='1500' className="servicespositioning__column__1">
                <h2 className="h2__servicespositioning__1">Analiza strony i konkurencji</h2>
                <h3 className="h3__servicespositioning__1">Przeprowadzona analiza strony oraz liderw w brany pozwoli na ukierunkowanie przyszych dziaa. Dziki analizie konkurencji poznajemy sposoby ich promocji.</h3>
            </div>
            <div data-aos='fade-down' data-aos-duration='1500' className="servicespositioning__column__2">
                <h2 className="h2__servicespositioning__1">Audyt seo</h2>
                <h3 className="h3__servicespositioning__1">Pierwszy krok do wysokiej pozycji w wyszukiwarce to profesjonalna analiza strony internetowej pod ktem technicznym.</h3>
            </div>
            <div data-aos='fade-left' data-aos-duration='1500' className="servicespositioning__column__3">
                <h2 className="h2__servicespositioning__1">Regularna optymalizacja strony</h2>
                <h3 className="h3__servicespositioning__1">Dopracowanie technicznej czci strony, wszystkich znacznikw meta, usuniciu bdnych podstron, przekierowa, odpowiednia optymalizacja treci.</h3>
            </div>
        </div>
        <div className="servicespositioning__row__2">
            <div data-aos='fade-right' data-aos-duration='1500' className="servicespositioning__column__1">
                <h2 className="h2__servicespositioning__1">Zarzdzanie Contentem</h2>
                <h3 className="h3__servicespositioning__1">Odpowiednia kreacja oraz optymalizacja treci na stronie pozwala nie tylko na zwikszenie widocznoci strony internetowej w wyszukiwarce Google ale i rwnie przycignicie uytkownikw na stron, dziki tzw. content marketing.</h3>
            </div>
            <div data-aos='fade-up' data-aos-duration='1500' className="servicespositioning__column__4">
                <h2 className="h2__servicespositioning__1">Content marketing</h2>
                <h3 className="h3__servicespositioning__1">Odpowiednia tre np. w postaci bloga jest pierwszym krokiem do sukcesu, jednak utworzon tre naley odpowiednio wypromowa poprzez tzw. dziaania on-site oraz off-site.</h3>
            </div>
            <div data-aos='fade-left' data-aos-duration='1500' className="servicespositioning__column__3">
                <h2 className="h2__servicespositioning__1">Link Building</h2>
                <h3 className="h3__servicespositioning__1">Budowa skutecznego profilu odnonikw do pozycjonowanej strony jest w sztuk sam w sobie, dlatego warto powierzy to dziaanie ekspertom, gdy linki niskiej jakoci mog powodowa efekt odwrotny ni zamierzony.</h3>
            </div>
        </div>
    </div>
  )
}

export default ServicesPositioning