import React, { useEffect } from 'react'
//CSS
import './WhoProgrammers.css'
//Packages
import $ from 'jquery'
import VanillaTilt from 'vanilla-tilt'
import Aos from 'aos'
import "aos/dist/aos.css" 

function WhoProgrammers() {
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="whoprogrammers__container">
        <div className="whoprogrammers__row">
            <div className="whoprogrammers__column__1">
                <h1 data-aos='fade-right' data-aos-duration='1500' className='h1__whoprogrammers__1'>Kim jest Front-End Developer?</h1>
                <h2 data-aos='fade-right' data-aos-duration='1500' className="h2__whoprogrammers__1">Ich gwnym zadaniem jest stworzenie programu, ktry atrakcyjnie wyglda i sprawnie dziaa. W dzisiejszych bardzo wanym elementem tworzenia oprogramowania jest responsywno. Front-end developerzy musz wic napisa kod, ktry zapewni sprawne dziaanie strony lub aplikacji na wszystkich urzdzeniach mobilnych i we wszystkich przegldarkach.</h2>
            </div>
            <div className="whoprogrammers__column__2">
                <h1 data-aos='fade-down' data-aos-duration='1500' className='h1__whoprogrammers__1'>Kim jest Back-End Developer?</h1>
                <h2 data-aos='fade-up' data-aos-duration='1500' className="h2__whoprogrammers__1">Programista back-end to osoba, ktra odpowiada za tworzenie kodu strony lub aplikacji. Przecitni uytkownicy nie widz jednak efektw jego pracy. Program mona porwna do organizmu czowieka  widzimy tylko to, co jest na zewntrz, ale najwaniejsze dla ycia procesy dziej si wewntrz. Tak samo back-end developer odpowiada za wydajno i poprawne funkcjonowanie produktu kocowego.</h2>
            </div>
            <div className="whoprogrammers__column__3">
                <h1 data-aos='fade-left' data-aos-duration='1500' className='h1__whoprogrammers__1'>Kim jest WordPress Developer?</h1>
                <h2 data-aos='fade-left' data-aos-duration='1500' className="h2__whoprogrammers__1">WordPress to system CMS, ktry oczywicie obsuguje back-end ale dostarcza te prosty system szablonowania. Chcc nie chcc, WordPress Developer to tak naprawd Full-Stack Developer wyspecjalizowany w tym konkretnym rodowisku. W rednich i duych projektach moe nastpi specjalizacja na Front-End WordPress Developera i Back-End WordPress Developera.</h2>
            </div>
        </div>
    </div>
  )
}

export default WhoProgrammers