import React, { useEffect } from 'react'
//CSS
import './Seo.css'
//Images
import SeoImg from '../../../Images/16.png'
//Packages
import $ from 'jquery'
import VanillaTilt from 'vanilla-tilt'
import Aos from 'aos'
import "aos/dist/aos.css" 


function Seo() {
  //VanillaTilt
  $(document).ready(function Main() { VanillaTilt.init(document.querySelectorAll(".seo__image__1")); });
  $(function() { VanillaTilt.init(document.querySelectorAll(".seo__image__1")); });
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 800;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="seo__container">
        <div className="seo__row">
            <div data-aos='fade-right' className="seo__column__1">
                <img className='seo__image__1' src={SeoImg} alt=''></img>
            </div>
            <div className="seo__column__2">
                <h1 data-aos='fade-left' className='h1__seo__1'>Pozycjonowanie stron internetowych (SEO)</h1>
                <h2 data-aos='fade-left' className='h2__seo__1'>Pozycjonowanie stron internetowych jest procesem, ktrego gwny cel polega na zwikszeniu widocznoci domeny w bezpatnych (organicznych) wynikach wyszukiwania pod wybranymi frazami kluczowymi. Dziaania te polegaj na optymalizacji wszystkich elementw witryny oraz budowania zaplecza backlinkw (link building). Na pozycjonowanie skadaj si liczne procesy dotyczce dziaa SEO, ktre przeprowadza si on-site (na stronie) oraz off-site (poza stron). Do off-site naley pozyskiwanie wartociowych linkw przekierowujcych do naszej witryny. On-site to: optymalizacja wszystkich publikowanych treci, kodu strony oraz wprowadzanie dziaa UX (User Expierience).</h2>
            </div>
        </div>
    </div>
  )
}

export default Seo