import React, { useEffect } from 'react'
//CSS
import './Time.css'
//Images
import TimeImage from '../../../Images/40.png'
//Packages
import $ from 'jquery'
import VanillaTilt from 'vanilla-tilt'
import Aos from 'aos'
import "aos/dist/aos.css" 

function Time() {
  //VanillaTilt
  $(document).ready(function Main() { VanillaTilt.init(document.querySelectorAll(".time__img__1")); });
  $(function() { VanillaTilt.init(document.querySelectorAll(".time__img__1")); });
  //Aos
  useEffect(() => {
  Aos.init({ duration: 2000 });
  }, []);
  //Aos disabled on mobile devices
  Aos.init({
  disable: function() {
  var maxWidth = 1400;
  return window.innerWidth < maxWidth;
  }
  });
  return (
    <div className="time__container">
        <div className="time__row">
            <div data-aos='fade-right' data-aos-duration='1500' className="time__column__1">
                <img className='time__img__1' src={TimeImage} alt=''></img>
            </div>
            <div className="time__column__2">
                <h1 data-aos='fade-left' data-aos-duration='1500' className="h1__time__1">Ile jestemy na rynku i jak dziaamy?</h1>
                <h2 data-aos='fade-left' data-aos-duration='1500' className="h2__time__1">Jestemy na rynku programistycznym od niedawna, co nie zniechca nas do dziaania, wiemy, e konkurencja jest ogromna, a my kochamy wyzwania! Jeli zdecydowae si na nasze usugi i zadzwonie na pocztku wysuchujemy czego potrzebujesz, tumaczymy wszystkie wane zagadnienia i dobieramy zesp programistw i grafikw do danego zlecenia. Na pocztku prosimy o wysanie nieduej zaliczki, pniej nasi programici od razu bior si do pracy. Jestemy elastyczni i to my dostosowywujemy si do klienta, a nie klient do nas, nie martwi nas nage zmiany w projekcie, pracujemy na bieco.</h2>
            </div>
        </div>
    </div>
  )
}

export default Time